<style lang="scss">
/* BREAKPOINTS FOR RESPONSIVE */
$breakpoints: (
  "tablette-min": 760px,
  "tablette-max": 1024px,
  "phone-max": 759px
);
/*MIXINS FOR RESPONSIVE*/
@mixin tablette {
  @media (min-width: map-get($map: $breakpoints, $key: "tablette-min")) and (max-width: map-get($map: $breakpoints, $key: "tablette-max")) {
    @content;
  }
}
@mixin phone {
  @media (max-width: map-get($map: $breakpoints, $key: "phone-max")) {
