<template>
  <v-container fluid>
    <div class="base">
      <Thumbnail v-if="video === false" @launch="setVideo($event)" />
      <video
        v-if="video != false"
        :src="videos[video].src"
        loop
        width="80%"
        class="video"
        controls
        autoplay
      ></video>
      <v-btn
        dark
        v-if="video"
        width="80%"
        id="offBtn"
        @click.prevent="setVideoOff()"
        >Retourner au Portfolio</v-btn
      >
    </div>
  </v-container>
</template>

<script>
import Thumbnail from "@/components/thumbnail.vue";

export default {
  name: "Portfolio",
  components: { Thumbnail },
  methods: {
    setVideo(Nb) {
      this.video = parseInt(Nb) - 3;
      window.scrollTo(0, 0);
    },
    setVideoOff() {
      this.video = false;
    }
  },
  data() {
    return {
      video: false,
      videos: [
        {},
        { src: require("@/assets/video/video4.mp4") },
        { src: require("@/assets/video/video5.mp4") },
        { src: require("@/assets/video/video6.mp4") }
      ]
    };
  }
};
</script>

<style lang="scss" scoped>
.base {
  height: auto;
  color: white;
  min-height: 100vh;
}
.video {
  margin-left: 10% !important;
}
#offBtn {
  margin-left: 10% !important;
  font-weight: bolder;
  font-size: 1.2em;
}
</style>
